
.markdown-text img {
  width: auto;
  max-width: 100%;
}
.detail {
  padding-top: 45px;
}
.detail .detail-body .detail-head {
    padding: 10px;
}
.detail .detail-body .detail-head .title {
      line-height: 25px;
      font-size: 18px;
      font-weight: bold;
}
.detail .detail-body .detail-head .avatar {
      padding: 10px 0;
}
.detail .detail-body .detail-head .avatar .user-img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
}
.detail .detail-body .detail-head .avatar .user-name {
        color: #0088cc;
        font-size: 14px;
}
.detail .detail-body .detail-head .avatar .time {
        float: right;
        line-height: 30px;
        color: #999;
        font-size: 12px;
}
.detail .detail-body .detail-content {
    padding: 10px;
}
.detail .detail-body .detail-content .markdown-text p {
      line-height: 20px;
      font-size: 14px;
      font-weight: bold;
}
.detail .detail-body .detail-content .markdown-text p a {
        color: #999;
        font-weight: normal;
}
.detail .detail-body .detail-content h2 {
      margin-top: 20px;
      margin-bottom: 15px;
      font-size: 36px;
      font-weight: bold;
}
.detail .detail-body .detail-content ul {
      margin: 0 0 10px 25px;
}
.detail .detail-body .detail-content ul li {
        line-height: 23px;
        list-style: disc;
        font-size: 16px;
        font-weight: bold;
}
.detail .detail-body .detail-content ul li li {
          list-style: circle;
}
.detail .detail-body .detail-content ul li a {
          display: block;
          line-height: 23px;
          font-size: 14px;
          font-weight: normal;
          color: #61b7a0;
}
.detail .detail-body .detail-comments-length {
    padding: 15px;
    background-color: #fbfbfb;
}
.detail .detail-body .detail-comments .comments-item {
    padding: 15px;
    border-top: 1px solid #ddd;
}
.detail .detail-body .detail-comments .comments-item .avatar {
      font-size: 0;
}
.detail .detail-body .detail-comments .comments-item .avatar > span {
        display: inline-block;
}
.detail .detail-body .detail-comments .comments-item .avatar .time {
        float: right;
        line-height: 30px;
        color: #999;
        font-size: 12px;
}
.detail .detail-body .detail-comments .comments-item .avatar .good {
        float: right;
        margin-right: 5px;
        line-height: 30px;
        color: #666;
        font-size: 12px;
}
.detail .detail-body .detail-comments .comments-item .avatar .good .iconfont {
          margin-right: 2px;
          font-size: 12px;
}
.detail .detail-body .detail-comments .comments-item .avatar .name {
        font-size: 14px;
}
.detail .detail-body .detail-comments .comments-item .avatar .name img {
          margin-right: 7px;
          width: 30px;
          height: 30px;
          border-radius: 50%;
}
.detail .detail-body .detail-comments .comments-item .avatar .name b {
          color: #61b7a0;
}
.detail .detail-body .detail-comments .comments-item .desc {
      padding-top: 5px;
      line-height: 20px;
      color: #666;
}
.detail .detail-body .detail-comments .comments-item .desc p {
        word-wrap: break-word;
}
.detail .detail-body .detail-comments .comments-item .desc a {
        color: #61b7a0;
}
.detail .detail-body .detail-comments .none-more {
    padding: 15px;
    border-top: 1px solid #ddd;
    text-align: center;
    color: #c3c3c3;
}



